import React from 'react'
import "./infinite-scroll.css"

export default class InfiniteScroll extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            showLoadingEnable: this.props.showLoadingButton ? this.props.showLoadingButton : true
        }

        this.onScroll = this.onScroll.bind(this)
    }

    update() {
        const distanceToBottom = document.documentElement.offsetHeight - (window.scrollY + window.innerHeight);
        this.setState({ showLoadingEnable: false })
        if (distanceToBottom < 100) this.props.onInfiniteScroll();
        this.ticking = false;
    }

    onScroll() {
        if (!this.ticking) {
            this.ticking = true
            requestAnimationFrame(() => this.update())
        }
    }

    componentDidMount() {
        window.addEventListener(`scroll`, this.onScroll)
    }

    componentWillUnmount() {
        window.removeEventListener(`scroll`, this.onScroll)
    }

    render() {
        if (!this.state.showLoadingEnable) return null;

        return <a className="more-btn"
            onClick={() => {
                this.onScroll();
                this.setState({ infinteScrollEnable: true })
            }}><span>More</span></a>
    }
}



