import React from "react"
import Link from "gatsby-link"

import PostItem from "../components/PostItem"
import InfiniteScroll from "../components/InfiniteScroll"

export default class IndexPage extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      postsToShow: 2
    }

    this.infiniteScrollHandler = this.infiniteScrollHandler.bind(this);
  }

  infiniteScrollHandler() {
    this.setState({ postsToShow: this.state.postsToShow + 2 });
  }

  render() {
    let posts = this.props.data.allMarkdownRemark.edges;

    return (
      <div style={{ marginTop: "-45px" }}>
        {posts.slice(0, this.state.postsToShow).map(({ node }) => (
          <PostItem post={node} key={node.id} />
        ))}
        
        {(this.state.postsToShow < posts.length) && (
          <div style={{
            textAlign: "center",
            paddingTop: "50px"
          }}>
            <InfiniteScroll onInfiniteScroll={this.infiniteScrollHandler} showLoadingButton={true}/>
          </div>
        )}
        
      </div>
    );
  }
}

export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          timeToRead
          frontmatter {
            date(formatString: "DD-MM-YYYY")
            path
            title
          }
        }
      }
    }
  }
  `;



